@import url(https://fonts.googleapis.com/css?family=Poppins:500,600);
#header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 3px solid #ecf0f1;
}

h1 {
  color: #353b48;
  font-style: italic;
  font-size: 45px;
}

p#username {
  display: inline-block;
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  font-size:16px;
  color: #353b48;
  margin-right: 20px;
}

img#avatar {
  width: 3rem;
  height: 3rem;
}
#canvas {
  border: 2px solid green;
}
#toolbar {
  display: flex;
  flex-direction: column;
  width: 100px;
  min-width: 100px;
  border-style: solid;
  border-width: 0 0 0 3px;
  border-color: #ecf0f1;
}

img.icons {
  width: 2rem;
  height: 2rem;
  align-self: center;
  margin: 10px 0;
}

div#call-buttons {
  display: flex;
  width: 300px;
  justify-content: space-between;
  position: absolute;
  top: 90%;
  left: 50%;
  z-index: 20;
}

div.round-bg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 3px solid #ecf0f1;
  border-radius: 50%;
  width: 70px;
  height: 70px;
}

img.call-buttons {
  width: 2rem;
  height: 2rem;
}
#video-container {
  display: flex;
  flex: 1 1;
  align-items: stretch;
  height: 100%;
}

#received-content {
  flex: 3 1;
  height: 100%;
  z-index: 5;
}

#received-video {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

#canvas-content {
  position: absolute;
  z-index: 10;
}

#local-content {
  position: absolute;
  z-index: 15;
}

#local-video {
  margin-top: 10px;
  margin-left: 10px;
  height: 150px;
  object-fit: contain;
  border: 5px solid #ecf0f1;
}
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  max-height: 100%;
}

#root {
  height: 100%;
  width: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

#overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 20;
}

#room {
  margin-bottom: 20px;
}

#chat {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

#messages {
  flex: 1 1;
}

#message-input {
  width: 100%;
  border-radius: 4px;
  padding: 16px 0;
}

#send-button {
  width: 100%;
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
}


div#join-room-wrapper {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

div#join-room-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  margin: auto;
  padding: 40px;
  border: 3px solid #ecf0f1;
}

img#profile-photo {
  width: 12rem;
  height: 12rem;
  margin-bottom: 3rem;
}

input#room-name {
  width: 60%;
  padding: 12px 0;
  margin: 8px 0;
  border: none;
  border-bottom: 3px solid #ecf0f1 ;
  color: #353b48;
  font-size: 14px;
}

input#room-name:focus {
  border: none;
  border-bottom: 3px solid #2ecc71 ;
} 

button#join {
  width: 60%;
  border: 3px solid #ecf0f1;
  color: #353b48;
  padding: 12px 0;
  background-color: white;
  text-transform: uppercase;
  font-size: 16px;
}

button#join:hover {
  background-color: #2ecc71;
  color: #ecf0f1;
}
* {
  margin: 0;
  padding: 0;
}

#routes-wrapper {
  width: 100%;
  height: 100%;
}
